export default {
    resolve( message, ajaxResponse ){
        let serverMsg = "Erreur inconnue";
        if( ajaxResponse ){
            serverMsg = ajaxResponse.body;

            if( ajaxResponse.status == 403 ){
                serverMsg = "Vous avez t dconnectez de l'application";
            }
        }
        return message + " (Rponse : " + serverMsg +")";
    }
}